.d_slideBlock {
  width: 370px;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
  position: relative;
}

.d_slideBlock .slick-dots {
  height: 0px;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  margin-left: 5px;
}

.d_slideBlock .slick-dots li button {
  max-height: 10px;
  max-width: 10px;
  border-radius: 12px;
  border: 2px solid #fb8381;
  margin-right: 8px;
}

.d_slideBlock .slick-active button:before {
  content: "";
  background-color: #fb8381;
  max-height: 10px;
  max-width: 10px;
  border-radius: 12px;
  border: 7px solid #fb8381;
  margin-right: 8px;
  opacity: 1;
}

.d_slideBlock .slick-dots li.slick-active button:before {
  opacity: 1.75;
}

.d_slideBlock .slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 20px;
  height: 20px;
  font-family: slick;
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: #000;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.d_slide {
