@media screen and (max-width: 1200px) {
  .d_flex {
    justify-content: center;
  }

  .d_divHexagon {
    width: 70%;
    height: 600px;
    left: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .d_slideBlock {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .d_divHexagon {
    width: 80%;
    height: 600px;
    left: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 780px) {
  .d_wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 768px) {
  .d_divHexagon {
    display: none;
  }

  .d_slideBlock {
    display: block;
    width: 90%;
  }
}

@media screen and (max-width: 576px) {
  .d_disFirstScreen {
    padding-top: 0px;
    padding-bottom: 20px;
    padding-left: 0;
    padding-right: 0;
    overflow: hidden;
  }

  .d_slideBlock {
    display: block;
    width: 100%;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0);
  }
}

@media screen and (max-width: 568px) {
  .d_flex {
    display: block;
  }
}

@media screen and (max-width: 520px) {
  .d_slideBlock .slick-dots {
    margin-bottom: 35px;
  }

  .d_slide__portrait {
    height: 370px;
  }
}